
@at-root {

  @-ms-viewport { /* stylelint-disable-line  at-rule-no-vendor-prefix */
    width: device-width;
  }
}

.page { // html
  box-sizing: border-box;
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%; /* stylelint-disable-line  property-no-vendor-prefix */
  min-width: 320px;
  min-height: 100%;

  &__inner {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  &__footer-wrapper {
    position: relative;
    flex-shrink: 0;
    z-index: -1;
    
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #ecf1d6;
  // display: flex;
  margin: 0;
  min-height: 100%;
  padding: 0;
  width: 100%;
}

[tabindex='-1']:focus {
  outline: none !important;
}

// iOS 'clickable elements' fix for role='button'
[role='button'] {
  cursor: pointer;
}

// Avoid 300ms click delay on touch devices that support the `touch-action` CSS property.
a,
// area,
button,
[role='button'],
input,
label,
select,
// summary,
textarea {
  touch-action: manipulation;
}

img {
  height: auto;
}

@media (min-width: 768px) {
.background {
  position: relative;
  z-index: 10;
  width: 100%;
  margin-right: 15px;
  background-image: url(../img/background.png);
  background-repeat: no-repeat;
  background-position: 5% 100%;
  -webkit-background-size: 98% 118%; 
  -o-background-size: 98% 118%; 
  background-size: 98% 118%; 
}
}


@media (min-width: 1200px) {
  .container {
    width: 960px;
    margin-left: auto;
    margin-right: auto;
  }

  .background {
    position: relative;
    z-index: 10;
    width: 100%;
    margin-right: 65px;
    background-image: url(../img/background.png);
    background-repeat: no-repeat;
    background-position: 10% 100%;
    -webkit-background-size: 95% 118%; 
    -o-background-size: 95% 118%; 
    background-size: 95% 118%; 
  }
}

.clearfix {
  content: '';
  display: table;
  clear: both;
}

.warning {
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  text-overflow: ellipsis;
  color: #70797C;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
  word-spacing: -0.7px;
  
  & span {
    font-size: 18px;
    font-weight: 900;
    color: #375855; 
  } 
}

@media(max-width: 767px) {
  .warning {
    width: 310px;
    margin: 0 auto 25px;
  }
}

@media (min-width: 768px) {
.warning {
  width: 90%;
  margin-bottom: 25px;
  margin-right: auto;
  margin-left: auto;
}
}

@media (min-width: 1200px) {
  margin-right: 0;
  margin-left: 0;
}


