
.stories {
	width: 100%;
	margin-bottom: 30px;
	
	&__title {
		margin-bottom: 43px;
		font-size: 24px;
		font-style: italic;
		font-weight: 900; 
		line-height: 24px;
		text-transform: uppercase;
		color: #A21F28; 
		text-align: center;
	}

	&__container {
		display: flex;
		-webkit-justify-content: center; 
		justify-content: center; 
		-webkit-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		width: 320px;
		margin: 0 auto;
	}
}

.video {
	width: 305px;
	height: 100%; 
	margin-bottom: 20px;
	
	&:last-child {
		margin-bottom: 0;
	}
}

@media (max-width: 767px) {
.stories {
	width: 100%;
	margin-bottom: 30px;
	
	&__title {
		margin-bottom: 43px;
		font-size: 24px;
		font-style: italic;
		font-weight: 900; 
		line-height: 24px;
		text-transform: uppercase;
		color: #A21F28; 
		text-align: center;
	}

	&__container {
		display: flex;
		-webkit-justify-content: center; 
		justify-content: center; 
		-webkit-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		width: 320px;
		margin: 0 auto;
	}
}
}

@media (min-width: 768px) {
	.stories {
	width: 100%;
	margin-bottom: 30px;
	
	&__title {
		margin-bottom: 43px;
		font-size: 24px;
		font-style: italic;
		font-weight: 900; 
		line-height: 24px;
		text-transform: uppercase;
		color: #A21F28; 
		text-align: center;
		
	  & br {
	  	display: none;
	  }
	}

	&__container {
		display: flex;
		-webkit-justify-content: space-around; 
		justify-content: space-around; 
		-webkit-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		width: 100%;
	}
}
}

@media (min-width: 1200px) {
.stories {
	margin-bottom: 0;
	
	&__title {
		margin-bottom: 43px;
		font-size: 24px;
		font-style: italic;
		font-weight: 900; 
		line-height: 24px;
		text-transform: uppercase;
		color: #A21F28; 
		text-align: center;
	}

	&__container {
		display: flex;
		-webkit-flex-wrap: nowrap;
		-ms-flex-wrap: nowrap;
		flex-wrap: nowrap;
		
	}

	&__center {
		margin-left: 23px;
		margin-right: 23px;
	}
}

.video {
	width: 305px;
	height: 100%; 
}

}