@media (max-width: 767px) {
	.page-header {
   width: 100%;
   margin-bottom: -120px 
  }

  .header-top {
    width: 100%;
    padding-top: 10px;
    background-color: #ba1f1f;
  }  

  & p {
    color: #fff; 
    font-size: 14px;
    margin-top: 0;
    margin-bottom: 0;
    line-height: 22px;
    text-align: center;    
  }
  
  .yellow-text {
    text-transform: uppercase;
    color: #FDE950;    
  }
  
  .shipping {
    font-style: italic;
  }

  .auto-yellow {
    width: 19px;
    height: 12px;    
  }

  .header-logo {    
      width: 100%;
      height: 290px;
      background-color: #fbfce1;
      -webkit-border-radius: 0 0 5px 0; 
      border-radius: 0 0 5px 0;
      border-top: 1px solid transparent;
  }

  .logo {
    width: 234px;
    height: 45px;
    margin: 20px auto 0;
    
  }
}

@media (min-width: 768px) {
	.page-header {
    width: 100%; 
    margin-bottom: -370px;
  }

  .header-top {
    width: 100%;
    height: 32px;
    background-color: #ba1f1f;    

    & p {
      color: #fff; 
      font-size: 14px;
      margin-top: 0;
      line-height: 32px;
      text-align: center;    
    }

    & br {
      display: none;
    }
  }
  
  .yellow-text {
    text-transform: uppercase;
    color: #FDE950;    
  }
  
  .shipping {
    font-style: italic;
  }

  .auto-yellow {
    width: 19px;
    height: 12px;    
  }

  .header-logo {    
      width: 100%;
      height: 558px;
      background-color: #fbfce1;
      -webkit-border-radius: 0 0 5px 0; 
      border-radius: 0 0 5px 0;
      border-top: 1px solid transparent;
  }

  .logo {
    width: 760px;
    height: 71px;
    margin: 20px auto 0;
    
  }
}

@media (min-width: 1200px) {

	.page-header {
    width: 100%;
    margin-bottom: -370px;  
  }

  .header-top {
  	width: 100%;
  	height: 32px;
  	background-color: #ba1f1f; 
	
  	& p {
  		color: #fff; 
  		font-size: 14px;
  		margin-top: 0;
  		line-height: 32px;
  		text-align: center;
  	}
  }

  .yellow-text {
  	text-transform: uppercase;
  	color: #FDE950;
  }

  .shipping {
  	font-style: italic ;
  }

  .auto-yellow {
  	width: 19px;
  	height: 12px;
  }

  .header-logo {
  	padding-right: 44px; 
    background-color: transparent;  
  	
  	&__back {
  	  width: 100%;
  	  height: 558px;
      margin-right: 30px;
  	  background-color: #fbfce1;
  	  -webkit-border-radius: 0 0 5px 0; 
  	  border-radius: 0 0 5px 0;
  	  border-top: 1px solid transparent;
  	}
  }

  .logo {
  	width: 1042px;
  	height: 71px;
  	margin-top: 20px;
  	margin-left: auto;
  	margin-right: auto;
  }
}
