
  .menu {
    display: flex;

  &__logo {
    display: block;
    height: 100%;    
  }

  &__container {
    display: flex;
    width: 450px;
  }

  &__link {
    display: block;
    margin-left: 3px;
    white-space: nowrap;
    width: 100%;
    font-size: 14px;
    font-weight: 400; 
    line-height: 34px; 
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 5px 14px;
    color: #375855;

    &:hover,
    &:focus { 
      color: #34919f;
    }
  }
}

@media (max-width: 767px) {
  .menu {
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-justify-content: center;
    justify-content: center;
    width: 315px;
    margin: 0 auto; 

  &__logo {
    display: block;
    width: 180px;
    height: 100%; 
    margin-bottom: 15px;   
  }

  &__container {
    -webkit-justify-content: center; 
    justify-content: center; 
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
  }
}
}

@media (min-width: 768px) {
  .menu {
  display: flex;
  -webkit-justify-content: space-around;
  justify-content: space-around;
  width: 90%;
  margin-right: auto;
  margin-left: auto;  
  margin-bottom: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid #cbd6be;
  
  &__logo {
    display: block;
    width: 463px;
    height: 100%;
    margin-left: 0;
  }

  &__container {
    -webkit-justify-content: flex-around; 
    justify-content: flex-around; 
    margin-right: 50px;
  }
}
}

@media (min-width: 1200px) {
.menu {
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  width: 90%;
  margin-right: auto;
  margin-left: auto;  
  margin-bottom: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid #cbd6be;
  
  &__logo {
    display: block;
    width: 463px;
    height: 100%;
    margin-left: 0;
  }

  &__container {
    -webkit-justify-content: flex-end; 
    justify-content: flex-end; 
    margin-right: -12px;
  }
}
}
