
.page-footer {
  position: relative;
  display: flex;
  width: 100%;
  padding-bottom: 42px;
  
  &__copyright {
    font-size: 12px;
    font-weight: 300;
    line-height: 24px; 
    color: #70797C;
    text-decoration: none; 
  }
  
  &__by-safe {
    position: absolute;
    bottom: -5px;
    left: 0;
  }

  &__certificates {
    display: flex; 
    -webkit-justify-content: flex-end; 
    justify-content: flex-end; 
    -webkit-align-items: center;
    align-items: center;
    width: 400px;
    height: 100%; 
    margin-top: 25px;
    
    & img {
      margin-left: 10px; 
      height: 100%;
    }
  }
}

@media (max-width: 767px) {
  .page-footer {
  -webkit-justify-content: space-around;
  justify-content: space-around;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
      
  &__certificates {
    -webkit-justify-content: center; 
    justify-content: center;

    & img {
      margin-left: 5px;
    }
    
  }
}
  
}

@media (min-width: 768px) {
  .page-footer {
  -webkit-justify-content: space-around;
  justify-content: space-around;
  
    &__copyright br {
      display: none;
    }
  }
}

@media (min-width: 1200px) {
.page-footer {
	-webkit-justify-content: space-between;
	justify-content: space-between;
}

}
