
.next {
	width: 100%;
	padding-bottom: 40px;
	margin-bottom: 30px;
	margin-left: auto;
	margin-right: auto;  
	border-bottom: 1px solid #88bca8;

	&__link {
		position: relative;
		float: right;
		margin-left: 2px;
		
		&::before {
			content: url(../img/go-to.png); 
		  position: absolute;
		  top: -2px;
		  left: -215px;
		  z-index: -1;
			
		}
	}
}

@media (max-width: 767px) {

.next {
	width: 320px;
}
}

@media (min-width: 768px) {
.next {
	width: 90%;
}
}

@media (min-width: 1200px) {
.next {
	width: 90%;
}
}