.product-list {
  display: flex;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  
  &--checked {
    margin-bottom: 39px;
  }
}

.product-card {
  position: relative;
  width: 312px;
  // height: 510px;
  margin-right: 4px;
  margin-left: 4px;
  margin-top: -10px;
  text-align: center; 
  
  &__wrapper {
    width: 312px;
    margin-bottom: 0px;
    background-color: #FFF;
    -webkit-border-radius: 3px 3px 3px 7px/4px; 
     border-radius: 3px 3px 3px 7px/4px;
    -webkit-transition-property: height, width, background, box-shadow; 
    -moz-transition-property: height, width, background, box-shadow; 
    -ms-transition-property: height, width, background, box-shadow; 
    -o-transition-property: height, width, background, box-shadow; 
    transition-property: height, width, background, box-shadow; 
    -webkit-transition-duration: 1s; 
    -moz-transition-duration: 1s; 
    -ms-transition-duration: 1s; 
    -o-transition-duration: 1s; 
    transition-duration: 1s; 
    -webkit-transition-timing-function: ease; 
    -moz-transition-timing-function: ease; 
    -ms-transition-timing-function: ease; 
    -o-transition-timing-function: ease; 
    transition-timing-function: ease; 
    
    &--checked {
      position: relative;
      z-index: 10;
      margin-top: -18px;
      padding-bottom: 1px;
      background: linear-gradient(#FFF, #FFF, #ffed70); 
      -webkit-border-radius: 5px;
      border-radius: 5px;
      -webkit-box-shadow: 0 0 3px 1px #c8c9b4;
      box-shadow: 0 0 5px 2px #c8c9b4;
      
      
      & > .product-card__border {
        width: 302px;
        margin: 4px auto; 
        border: 2px solid #43625f ;
        -webkit-border-radius: 4px; 
        border-radius: 4px; 
      }

      & .product-card__img {
        margin-top: -16px;
      }
    }
  }

  &__img {
    width: 312px;
    height: 217px;
    margin-top: -33px;
    
    &--checked {
      margin-top: -20px;
    }
  }

  &__title {
    margin-top: 12px;
    margin-bottom: 12px;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 26px;
    font-weight: 400; 
    line-height: 20px;
    color: #b1a78e;
  } 

  &__description {
    display: block;
    width: 100%; 
    font-family: PT Sans Narrow, sans-serif; 
    font-size: 15px;  
    letter-spacing: 0.2px;
     
    color: #757879;
  }


  &__quality {    
    position: relative;

    &::before {
      content: url(../img/selection.png);
      position: absolute;
      left: -35px;
      top: 2px;
      display: block;
      width: 25px;
      height: 24px;
      
    }

    & span {
      font-style: italic;
      text-transform: uppercase;
      
      color: #34919F;
    }
  }

  &__auto {
    position: relative;
    margin-left: 25px;
    font-style: italic;
    text-transform: uppercase;
    color: #C82044;
    
    &::before {
      left: -60px;
    }
    
    &::after {
      content: url(../img/auto-pink.png);
      position: absolute;
      left: -28px;
      top: 2px;
      display: block;
      width: 26px;
      height: 17px;
    }
  }

  &__quality-box {
    width: 230px;
    margin: 0 auto; 
    padding-left: 45px;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 18px;
    font-weight: 900; 
    line-height: 30px;
    text-align: left; 
    
    &--checked .product-card__quality::before {
        content: url(../img/check-quality.png); 
        position: absolute;
        left: -35px;
        top: 2px;
        display: block;
        width: 25px;
        height: 24px;      
    }

    &--checked > .product-card__auto::before {
      left: -60px;
    }
  }
}

// .product-card__quality-box--checked .{}

.most-popular {
  position: relative;

  
    &::before {
      content: url(../img/most-popular.png);
      position: absolute;
      left: -9px;
      top: -9px;
      // display: block;
      width: 109px;
      height: 109px;
    }
    
    &::after {
      content: url(../img/pop-up-text.png);
      position: absolute;
      left: 137px;
      top: -57px;
      // display: block;
      z-index: 1;
      width: 109px;
      height: 109px;
    }
}

.price {
  font-family: 'Roboto Condensed', sans-serif;
  width: 100%;
  padding-top: 10px;
  background-image:  url(../img/form-border.png);
  background-repeat: no-repeat;
  background-position: 4px 0; 

  &__retail {    
  }

  &__without-discount {
    margin-top: 7px;
    margin-bottom: 0;
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 0; 
    color: #43625F; 
  }

  &__red-line {
    position: relative;
    
    &:before {
      content: '';
      position: absolute;
      left: -2px;
      top: 8px;
      display: block;
      width: 60px;
      height: 2px;
      background-color: #a92d16;
      -webkit-transform: rotate(8deg); 
      -moz-transform: rotate(8deg); 
      -ms-transform: rotate(8deg); 
      -o-transform: rotate(8deg); 
      transform: rotate(8deg); 
    }
  }

  &__discount {
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    padding-left: 22px;
    font-size: 32px;
    font-weight: 700;
    letter-spacing: 0; 
    color: #43625F; 
 
    
    &--insert {
      margin-right: 7px;
      line-height: 39px; 
    }
    
    &-quantity {
      width: 60px;
      height: 28px;
      margin-top: 5px;
      padding: 0 4px;
      font-size: 14px;
      line-height: 14px;
      text-align: left;

      
      &--checked {
        background: linear-gradient(to right, #FFF, transparent); 
      }
    }
  }

  &--checked {
    width: 302px;
    height: 120px;
    background-image:  url(../img/check-price.png);
    background-position: -3px -1px; 
    -webkit-border-radius: 0 0 5px 5px; 
    border-radius: 0 0 3px 2px; 

    & .price__without-discount {
      color: #FFED70;
    }

    & .price__discount-euro {
      color: #FFF; 
    }
  }
}

.hidden {
  display: none;
}

.select {
  float: right;
  position: relative;
  display: inline-block;
  margin-right: 48px;
  padding-top: 5px;
  padding-bottom: 10px;
  font-size: 16px;
  font-style: italic;
  font-weight: 900; 
  line-height: 23px;
  text-transform: uppercase;
  color: #43625F; 
  
  & > .selected {
    display: none;
  }
  
  &__checkbox {
    display: none;
  }
  
  &__checkbox + &__chip {
    content: '';
    position: absolute;
    top: 5px;
    left: 125px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    -webkit-box-shadow: inset 0 1px 1px #464646;
    box-shadow: inset 0 1px 1px #464646;
  }

  &__checkbox:checked + &__chip::before {
    content: url(../img/select.png);
    position: absolute;
    top:-8px;
    left: 0px;
    width: 26px;
    height: 26px;
    -webkit-transition: 0.2s ease 0.1s; 
    -moz-transition: 0.2s ease 0.1s; 
    -ms-transition: 0.2s ease 0.1s; 
    -o-transition: 0.2s ease 0.1s; 
    transition: 0.2s ease 0.1s; 
  }

  &__checkbox + &__chip:hover {
    content: '';
    position: absolute;
    top: 5px;
    left: 125px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    -webkit-box-shadow: inset 0 1px 1px 1px rgba(70, 70, 70, .7);
    box-shadow: inset 0 1px 1px 1px rgba(70, 70, 70, .7);
  }

  &__checkbox:checked + &__chip:hover::before {
    content: url(../img/select.png);
    position: absolute;
    top:-8px;
    left: 0px;
    display: block;
    width: 26px;
    height: 26px;
    -webkit-transform-origin: 50% 80%; 
    -moz-transform-origin: 50% 80%; 
    -ms-transform-origin: 50% 80%; 
    -o-transform-origin: 50% 80%; 
    transform-origin: 50% 80%; 
    -webkit-transform: rotate(5deg); 
    -moz-transform: rotate(5deg); 
    -ms-transform: rotate(5deg); 
    -o-transform: rotate(5deg); 
    transform: rotate(5deg); 
  }

  &__checkbox + &__chip:disabled {
    content: '';
    position: absolute;
    top: 5px;
    left: 125px;
    width: 20px;
    height: 20px;
    background-color: #404040;
    // border: 1px solid #000;
    border-radius: 50%;
    -webkit-box-shadow: inset 0 1px 1px #464646;
    box-shadow: inset 0 1px 1px #464646;
    }
  
  &__checkbox:checked + &__chip::before:disabled {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    width: 12px;
    height: 12px;
    background: rgba(248, 247, 244, .4);
    border-radius: 50%;
  }
  
  &--checked {
    width: 217px;
    height: 36px;
    margin: 15px 0 0 68px;
    padding-top: 8px; 
    background-image: url(../img/check-select.png); 
    
    & > .selected {
      display: block;
    }

    & > .unselected {
      display: none;
    }
    
    & .select__checkbox + .select__chip {
      content: '';
      position: absolute;
      top: 8px;
      left: 180px;
      width: 20px;
      height: 20px;
      border: 2px solid #43625f;
      border-radius: 50%;
      -webkit-box-shadow: none;
      box-shadow: none;
      
    }

    & select__checkbox:checked + select__chip:hover::before {
      content: url(../img/select.png);
      position: absolute;
      top:-9px;
      left: -2px;
      display: block;
      width: 26px;
      height: 26px;
      -webkit-transform-origin: 50% 80%; 
      -moz-transform-origin: 50% 80%; 
      -ms-transform-origin: 50% 80%; 
      -o-transform-origin: 50% 80%; 
      transform-origin: 50% 80%; 
      -webkit-transform: rotate(5deg); 
      -moz-transform: rotate(5deg); 
      -ms-transform: rotate(5deg); 
      -o-transform: rotate(5deg); 
      transform: rotate(5deg); 
    }
  }
}

.amazon {
