  &__link {
    margin-right: 10px;
    font-size: 12px;
    font-style: italic;
    line-height: 18px;
    color: #A1A1A1;
    text-align: right;
    
    & img {
      position: relative;
      top: 5px;
      margin-left: 3px;
    }
  }
