
.modal {
  position: absolute;
  top: 120px;
  left: 50%;
  z-index: 20;
  width: 300px;
  margin-left: -230px;
  padding: 50px 80px;
  color: #000;
  // background: #f8f3f0 url("../img/back.png") repeat;
  box-shadow: 0 30px 50px rgba(0,0,0,0.7);
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}

.modal-close {
  position: absolute;
  top: 0;
  right: -35px;
  width: 22px;
  height: 22px;
  font-size: 0;
  background-color: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
}

.modal-close::before,
.modal-close::after {
  content: '';
  position: absolute;
  top: 10px;
  left: 2px;
  width: 19px;
  height: 3px;
  background-color: #d0d0d0;
  border-right: 1px;
  -webkit-transition: 1s ease-out 0.5s; 
  -moz-transition: 1s ease-out 0.5s; 
  -ms-transition: 1s ease-out 0.5s; 
  -o-transition: 1s ease-out 0.5s; 
  transition: 1s ease-out 0.5s; 
}

.modal-close::before {
  transform: rotate(45deg);
}

.modal-close::after {
  transform: rotate(-45deg);
}

.modal-close:hover {
  -webkit-transform: rotate(360deg); 
  -moz-transform: rotate(360deg); 
  -ms-transform: rotate(360deg); 
  -o-transform: rotate(360deg); 
  transform: rotate(360deg); 
}

.modal-overlay,
.modal {
  display: none;
}

.modal-show {
  display: block;
  animation: bounce 0.6s;
}

